.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #333;
    padding: 15px;
    color: white;
  }
  .nav-links {
    list-style: none;
    display: flex;
  }
  .nav-links li {
    margin: 0 15px;
  }
  .nav-links a {
    color: white;
    text-decoration: none;
    font-weight: bold;
  }
  